<template>
    <div class="v-select">
        <div 
            class="result-size"
            v-on:click="show = !show"
        >
        Size
        <img class="select-arrow" src="../../src/ico/arrow.svg" alt="">
        </div>
        <transition name="fade">
            <div class="list-size" v-if="show">
                <p class="v-option"
                    v-for=" size in getSize" 
                    :key="size.name"
                    >
                    {{size.name}}
                </p>
            </div>
        </transition>
    </div>
</template>

<script>

    export default {
        name: 'v-select',
            computed: {
                getSize () {
                    return this.$store.state.sizes
            }
        },
        data: function() {
            return  { show: false
            }
        },
    }
</script>

<style lang="sass">

