    div.v-select
        margin-right: 20px

    div.result-size
        display: flex
        justify-content: space-between
        align-items: center
        width: 125px
        height: 50px
        padding: 0px 20px
        border: solid 1px #e4e4e4
        font-family: 'DIN Next LT Pro'
        font-size: 14px
        font-weight: 500
        font-style: normal
        background-color: #FFFFFF
        color: #666666
        cursor: pointer
        position: relative

    div.list-size
        font-family: 'DIN Next LT Pro'
        font-size: 14px
        font-weight: 500
        font-style: normal
        border: solid 1px #e4e4e4
        position: absolute
